import React from 'react';
import {Grid} from '@material-ui/core';
import {useEffect, useState} from 'react';
import MaterialTable from 'material-table';
import api from '../../utils/axios';
import {gridSpacing} from '../../store/constant';
import toast, {Toaster} from 'react-hot-toast';
import Cookies from 'js-cookie';

const Category = () => {
    const pageSize = 1000;
    const [isUpdate, setUpdate] = useState(false);
    const [subCategoryData, setSubCategoryData] = useState([]);
    const [categoryData, setCategoryData] = useState([]);
    const [token] = useState(Cookies.get('strAccessToken'));
    let headers = {
        headers: {
            authorization: `Bearer ${token}`
        }
    };
    useEffect(() => {
        async function fetchGetData() {
            const response = await api.post('productSubCategory/getProductSubCategory');
            setSubCategoryData(response.data);
            return response.data;
        }
        fetchGetData();
        async function fetchCategoryGetData() {
            const response = await api.post('productCategory/getProductCategory');
            setCategoryData(response.data);
            return response.data;
        }
        fetchCategoryGetData();
    }, [isUpdate]);

    let DataCategory = {};
    categoryData.forEach((items) => {
        DataCategory[items.intProductCategoryPk] = items.strProductCategoryName;
    });
    const columns = [
        {field: 'id', title: 'id', editable: false},
        {
            field: 'strProductSubCategoryName',
            title: 'Sub Category',
            validate: (rowData) =>
                rowData.strProductSubCategoryName === undefined
                    ? {isValid: false, helperText: 'Sub Category cannot be empty'}
                    : rowData.strProductSubCategoryName === ''
                    ? {isValid: false, helperText: 'Sub Category cannot be empty'}
                    : true
        },
        {
            field: 'strCategoryId',
            title: 'Category',
            lookup: DataCategory,
            validate: (rowData) =>
                rowData.strCategoryId === undefined
                    ? {isValid: false, helperText: 'Category cannot be empty'}
                    : rowData.strCategoryId === ''
                    ? {isValid: false, helperText: 'Category cannot be empty'}
                    : true
        },
        {field: 'datCreatedDate', title: 'Added date', editable: false},
        {field: 'datModifiedDate', title: 'Updated date', editable: false}
    ];
    function tConvert(time) {
        // Check correct time format and split into components
        time = time.toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
        if (time.length > 1) {
            // If time format correct
            time = time.slice(1); // Remove full string match value
            time[5] = +time[0] < 12 ? 'AM' : 'PM'; // Set AM/PM
            time[0] = +time[0] % 12 || 12; // Adjust hours
        }
        return time.join(''); // return adjusted time or original string
    }
    let arrData = subCategoryData
        ? subCategoryData.map((item) => {
              if (item.datCreatedDate !== []) {
                  if (item.datCreatedDate.indexOf('T') !== -1) {
                      let arrDateAndTime = item.datCreatedDate.split('T');
                      arrDateAndTime[1] = tConvert(arrDateAndTime[1].split('.')[0]);
                      arrDateAndTime = arrDateAndTime[0] + ' / ' + arrDateAndTime[1];
                      item.datCreatedDate = arrDateAndTime;
                  }
              }
              if (item.datModifiedDate !== null) {
                  if (item.datModifiedDate.indexOf('T') !== -1) {
                      let arrDateAndTime = item.datModifiedDate.split('T');
                      arrDateAndTime[1] = tConvert(arrDateAndTime[1].split('.')[0]);
                      arrDateAndTime = arrDateAndTime[0] + ' / ' + arrDateAndTime[1];
                      item.datModifiedDate = arrDateAndTime;
                  }
              }
              return item;
          })
        : subCategoryData;
    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item lg={12} md={12} sm={6} xs={12}>
                        <Toaster
                            toastOptions={{
                                className: '',
                                style: {
                                    borderRadius: '1rem',
                                    padding: '1rem',
                                    fontSize: '1rem',
                                    fontWeight: 1000,
                                    color: '#ffff',
                                    backgroundColor: '#8BCC51'
                                }
                            }}
                        />
                        <MaterialTable
                            title="Sub Categories"
                            data={arrData}
                            columns={columns}
                            options={{
                                pageSizeOptions: [50, 100, 200, 500, 1000],
                                pageSize: pageSize,
                                sorting: true,
                                exportButton: true,
                                actionsColumnIndex: -1,
                                addRowPosition: 'first',
                                loadingType: 'linear'
                            }}
                            editable={{
                                onRowAdd: (newRow) =>
                                    new Promise((resolve, reject) => {
                                        async function fetchData() {
                                            try {
                                                let params = {
                                                    intCategoryId: newRow['strCategoryId'],
                                                    strProductSubCategoryName: newRow['strProductSubCategoryName']
                                                };
                                                const response = await api.post(
                                                    'productSubCategory/createProductSubCategory',
                                                    params,
                                                    headers
                                                );
                                                toast('Created successfully.');
                                                return response;
                                            } catch (err) {
                                                if (err.error === 'PRODUCT_SUB_CATEGORY_ALREADY_EXIST') {
                                                    toast('sub category is already exist in the table !', {
                                                        style: {
                                                            backgroundColor: '#EE3434'
                                                        }
                                                    });
                                                }
                                            }
                                        }
                                        fetchData();
                                        async function fetchGetData() {
                                            const response = await api.post('productSubCategory/getProductSubCategory');
                                            setSubCategoryData(response.data);
                                            return response;
                                        }
                                        fetchGetData();
                                        setTimeout(() => {
                                            setUpdate(!isUpdate);
                                            resolve();
                                        }, 500);
                                    }),
                                onRowDelete: (selectedRow) =>
                                    new Promise((resolve, reject) => {
                                        let intProductSubCategoryPk = selectedRow['intProductSubCategoryPk'];
                                        let deleteParams = {
                                            intProductSubCategoryPk: intProductSubCategoryPk
                                        };
                                        async function fetchData() {
                                            const response = await api.delete('productSubCategory/deleteProductSubCategory', {
                                                data: deleteParams,
                                                headers: {
                                                    authorization: `Bearer ${token}`
                                                }
                                            });
                                            return response;
                                        }
                                        fetchData();
                                        toast('Deleted successfully.', {
                                            style: {
                                                backgroundColor: '#D82D8A'
                                            }
                                        });
                                        async function fetchGetData() {
                                            const response = await api.post('productSubCategory/getProductSubCategory');
                                            setSubCategoryData(response.data);
                                            return response;
                                        }
                                        fetchGetData();
                                        setTimeout(() => {
                                            setUpdate(!isUpdate);
                                            resolve();
                                        }, 500);
                                    }),
                                onRowUpdate: (updatedRow, oldRow) =>
                                    new Promise((resolve, reject) => {
                                        if (
                                            !(updatedRow.strProductSubCategoryName === oldRow.strProductSubCategoryName) ||
                                            !(updatedRow.strCategoryId === oldRow.strCategoryId)
                                        ) {
                                            let params = {
                                                intCategoryId: updatedRow['strCategoryId'],
                                                intProductSubCategoryPk: updatedRow['intProductSubCategoryPk'],
                                                strProductSubCategoryName: updatedRow['strProductSubCategoryName']
                                            };
                                            async function fetchData() {
                                                try {
                                                    const response = await api.put(
                                                        'productSubCategory/updateProductSubCategory',
                                                        params,
                                                        headers
                                                    );
                                                    toast('Updated successFully.', {
                                                        style: {
                                                            backgroundColor: '#2DAAD8'
                                                        }
                                                    });
                                                    return response;
                                                } catch (err) {
                                                    if (err.error === 'PRODUCT_SUB_CATEGORY_ALREADY_EXIST') {
                                                        toast('Product is already exist in the table !', {
                                                            style: {
                                                                backgroundColor: '#EE3434'
                                                            }
                                                        });
                                                    }
                                                }
                                            }
                                            fetchData();
                                        }
                                        async function fetchGetData() {
                                            const response = await api.post('productSubCategory/getProductSubCategory');
                                            setSubCategoryData(response.data);
                                            return response;
                                        }
                                        fetchGetData();
                                        setTimeout(() => {
                                            setUpdate(!isUpdate);
                                            resolve();
                                        }, 500);
                                    })
                            }}
                        />
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default Category;
